import React from "react";
import {CollapseDropdown} from "../Dropdown";
const pdf = "reports/EOA/EOA23-24.pdf";

export const MonitoringReports = () => {
	const monitoringReport = [
		{name: "Monitoring Report 2020 - 2021", path: pdf},
		{name: "Monitoring Report 2019 - 2020", path: pdf},
		{name: "Monitoring Report 2018 - 2019", path: pdf},
	];
	return (
		<>
			<h1 className="sub-heading">Monitoring Reports</h1>
			{monitoringReport.map((ra) => (
				<CollapseDropdown data={ra} />
			))}
		</>
	);
};
