export const Dashboard = () => {
	const {page} = useParams();
	const [staffData, setStaffData] = useState();
	const fetchstaffData = async () => {
		console.log("working");
		let response = await fetch(
			"https://gpsakoli.ac.in/public/api/connection.php?staff-details",
			{method: "GET"}
		);

		let data = await response.json();
		console.log(data);
		setStaffData(data.data);
	};
	const onDelete = async (i) => {
		console.log("working");
		let response = await fetch(
			"https://gpsakoli.ac.in/public/api/connection.php?action=deleteFacultyDetails&id=" +
				i +
				1,
			{method: "GET"}
		);

		let data = await response.text();
		fetchstaffData();
		console.log(data);
	};
	useEffect(() => {
		fetchstaffData();
	}, []);

	// window.onload = () => {
	return (
		<>
			<div
				className="dashboard-container grid"
				style={{gridTemplateColumns: "250px 100%"}}>
				<div>
					<Sidebar />
				</div>
				<div className="dashboard-content p-4">
					{page === "staff-data" && (
						<DashboardTable
							staffData={staffData}
							onDelete={onDelete}
						/>
					)}
					{page === "record-staff" && <FacultyForm />}
				</div>
			</div>
		</>
	);
};
