import React from "react";

import {CollapseDropdown} from "../Dropdown";
import {useParams} from "react-router-dom";
export const DeptPortfolio = ({deptPortfolioData}) => {
	const {branch} = useParams();
	const Data = [
		{
			name: "2023-2024",
			path: "department-portfolio/computer/2324dpcm.pdf",
			showCD: true,
		},
		{
			name: "2022-2023",
			path: "department-portfolio/computer/2223dpcm.pdf",
		},
		{
			name: "2021-2022",
			path: "department-portfolio/computer/2122dpcm.pdf",
		},
	];
	return (
		<>
			<h1 className="heading heading-top-left-line">
				Department Portfolio
			</h1>
			{deptPortfolioData.map(
				(dp) => dp.branch === branch && <CollapseDropdown data={dp} />
			)}
			{console.log(deptPortfolioData)}
		</>
	);
};
