import React from "react";
import {CollapseDropdown} from "../Dropdown";

export default function DeptTimeTable() {
	const deptTimeTable = [
		{name: "Even: 2022-23", path: "timetable/computer/Even2022-23.pdf"},
		{
			name: "Odd: 2022-23",
			path: "timetable/computer/CM timetable 2022-23.pdf",
		},
		{name: "Even: 2021-22", path: "timetable/computer/Cm timetable.pdf"},
	];
	return (
		<>
			<h1 className="heading heading-top-left-line">Time Table</h1>
			{deptTimeTable.map((tt) => (
				<CollapseDropdown data={tt} />
			))}
		</>
	);
}
