import React from "react";
import {CollapseDropdown} from "../Dropdown";
const pdf = "resultAnalysis/computer/2ndsem.pdf";
export default function DeptResultAnalysis() {
	const deptResultAnalysisData = [
		{name: "Winter 2022 ", path: pdf},
		{name: "Summer 2022", path: pdf},
		{name: "Winter 2019-2020", path: pdf},
		{name: "Summer 2019-2020", path: pdf},
		{name: "Winter 2018-2019", path: pdf},
		{name: "Summer 2018-2019", path: pdf},
		{name: "Winter 2017-2018", path: pdf},
		{name: "Summer 2017-2018", path: pdf},
	];
	return (
		<>
			<h1 className="heading">Result Analysis</h1>
			{deptResultAnalysisData.map((ra) => (
				<CollapseDropdown data={ra} />
			))}
		</>
	);
}
